import anime from "animejs"

import RandomString from "../../medical-advices/medical-advices"

import "../stylesheets/moving-letters-new.css"

function movingLettersNew() {

  window.addEventListener('load', () => {



// Wrap every letter in a span
var textWrapper = document.querySelector('.ml12');
if(textWrapper !== null || undefined)  {
textWrapper.innerHTML = textWrapper.textContent.replace(/\S/g, "<span class='letter'>$&</span>");
} else return


try {

anime.timeline({loop: true})
  .add({
    targets: '.ml12 .letter',
    translateX: [40,0],
    translateZ: 0,
    opacity: [0,1],
    easing: "easeOutExpo",
    duration: 1200,
    delay: (el, i) => 500 + 30 * i
  }).add({
    targets: '.ml12 .letter',
    translateX: [0,-30],
    opacity: [1,0],
    easing: "easeInExpo",
    duration: 1100,
    delay: (el, i) => 100 + 30 * i
  }) } catch(e) {
    console.log('undefined')
  }


})

    return (
        <>

<h1 class="ml12"> <RandomString /> </h1>

         </>
    )
}

export default movingLettersNew